@import "../../styles/sass/variables";

.title {
  color: $titleTextColor;
  margin-bottom: 15px;
  font-size: 20px;
  line-height: 1.2;
  text-align: center;
  @media screen and (max-width: 432px) {
    font-size: 18px;
  }
  @media screen and (max-width: 344px) {
    font-size: 15px;
  }
}